<template>
  <div class="grayPageBG">
    <van-nav-bar
      ref="topBar"
      title="任务审核"
      left-text="返回"
      fixed
      left-arrow
      @click-left="onNavBarLeftPressed"/>
    <div class="under-nav overflow-auto" :style="containerHeight" v-if="missionAcceptDetail">
      <div v-if="missionAcceptDetail" style="background-color: white">
        <div style="float: right;margin-right: 20px">
          <span style="font-size: 30px;color: crimson" v-show="missionAcceptDetail.proceedPropertyId === 23">{{missionStatusText}}</span>
          <span style="font-size: 30px;color: lawngreen"
                v-show="missionAcceptDetail.proceedPropertyId === 24">{{missionStatusText}}</span>
        </div>
        <div>
          <span>标 题：</span>
          <span>{{missionAcceptDetail.title}}</span>
        </div>
        <div>
          <span>编 号：</span>
          <span>{{missionAcceptDetail.id}}</span>
          <span class="category">类 别：</span>
          <span>{{getMissionTypeName()}}</span>
        </div>
        <div>
          <span>单 价：</span>
          <span>{{missionAcceptDetail.price / 100}}元</span>
        </div>
        <div>
          <span>文字验证：</span>
          <span>{{missionAcceptDetail.textVerify}}</span>
        </div>
        <div v-if="missionAcceptDetail.url">
          <span>链接：</span>
          <a :href="missionAcceptDetail.url"><span style="text-decoration: underline;">点击查看任务链接</span></a>
        </div>
        <div>
          <span>备 注：</span>
          <span>{{missionAcceptDetail.remark}}</span>
        </div>
        <div>
          <span>置顶到期：</span>
          <span>{{missionAcceptDetail.topEndTime}}</span>
        </div>
        <div>
          <span>截止时间：</span>
          <span>{{missionAcceptDetail.deadlineTime}}</span>
        </div>
      </div>
      <div class="align-center">
        <div>
          <span>验证图</span>
          <span>(共{{missionAcceptDetail.uploadImgList.length}}张)</span>
        </div>
        <div v-for="(item,index) in missionAcceptDetail.uploadImgList" :key="'sample' + index">
          <img class="screen-img" :src="imgFullPath(item)" alt=""/>
          <div>
            <span>图{{index + 1}}</span>
          </div>
        </div>
        <div>
          <span>文字验证：</span>
          <span>{{missionAcceptDetail.textVerifyFromUser}}</span>
        </div>
      </div>
    </div>

    <div v-if="missionAcceptDetail" class="submit" ref="bottomBar"
         v-show="missionAcceptDetail.proceedPropertyId === 22 && missionAcceptDetail.publishUserId === userId">
      <van-button type="danger" round size="large" class="left-button" @click="onNotPassMissionButtonPressed">不合格
      </van-button>
      <van-button type="primary" round size="large" @click="onPassMissionButtonPressed">合格</van-button>
    </div>

    <div v-if="missionAcceptDetail" class="submit" ref="bottomBar"
         v-show="missionAcceptDetail.proceedPropertyId === 23 && missionAcceptDetail.canComplaint">
      <van-button type="primary" round size="large" @click="complaintMission">申诉</van-button>
    </div>

  </div>

</template>

<script>
  import { REVIEW_MISSION_DETAIL_URL, REVIEW_MISSION_URL } from "@/config/host"
  import { getPropertyById } from "@/service/property"
  import { mapGetters } from "vuex"
  import { Dialog } from "vant"

  export default {
    name: "MissionReview",
    data () {
      return {
        acceptId: null,
        missionAcceptDetail: null,
        containerHeight: {
          height: "100%"
        },
        showComplaintDialog: false
      }
    },
    props: {},
    computed: {
      ...mapGetters([
        "userId"
      ]),
      missionStatusText () {
        if (this.missionAcceptDetail.proceedPropertyId === 23) {
          if (this.missionAcceptDetail.canComplaint) { // 没有申诉过
            return "不合格"
          } else { // 申诉过
            switch (this.missionAcceptDetail.complaintResult) {
              case null:
                return "申诉处理中"
              case false:
                return "终审不合格"
            }
          }
        }
        if (this.missionAcceptDetail.proceedPropertyId === 24) {
          if (this.missionAcceptDetail.complaintResult) {
            return "终审合格"
          }
          return "已完成"
        }
        return ""
      }
    },
    async mounted () {
      this.acceptId = this.$route.query.id
      const result = await this.httpPostWithLoading(REVIEW_MISSION_DETAIL_URL, { id: this.acceptId })
      if (result.success) {
        this.missionAcceptDetail = result.data
      }
      this.containerHeight.height = window.innerHeight - this.$refs.topBar.offsetHeight - (this.$refs.bottomBar ? this.$refs.bottomBar.offsetHeight : 0) + "px"
    },
    methods: {
      onNavBarLeftPressed () {
        this.$router.back()
      },
      async onPassMissionButtonPressed () {
        const result = await this.httpPostWithLoading(REVIEW_MISSION_URL, {
          missionAcceptId: this.missionAcceptDetail.id,
          result: true
        })
        if (result.success) {
          this.toastSuccess("审核完成")
          this.$router.back()
        }
      },
      async onNotPassMissionButtonPressed () {
        const result = await this.httpPostWithLoading(REVIEW_MISSION_URL, {
          missionAcceptId: this.missionAcceptDetail.id,
          result: false
        })
        if (result.success) {
          this.toastSuccess("审核完成")
          this.$router.back()
        }
      },
      getMissionTypeName () {
        const p = getPropertyById(this.missionAcceptDetail.typePropertyId)
        return p.name
      },
      complaintMission () {
        Dialog.confirm({
          title: "提示",
          message: "申诉内容商家看不到，不符合申诉条件的将不会受理，您确认是要申诉吗？"
        }).then(() => {
          this.$router.push({ name: "Complaint", query: { id: this.acceptId } })
        })
      }
    }
  }
</script>

